[class*=uk-icon-]:before {
	font-family: var(--icon);
	display: inline-block;
	font-weight: 400;
	font-style: normal;
	line-height: 1;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale
}

[class*=uk-icon-]:before,
[class*=uk-icon-]:focus:before,
[class*=uk-icon-]:hover:before {
	text-decoration: none
}

.uk-icon:before {
	display: inline-block;
	font: normal normal normal 1rem/1 var(--icon);
	text-rendering: auto;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	text-align: center;
	vertical-align: middle
}

.uk-icon-lg {
	font-size: 1.33333em;
	line-height: .75em;
	vertical-align: -15%
}

.uk-icon-2x {
	font-size: 2em
}

.uk-icon-3x {
	font-size: 3em
}

.uk-icon-4x {
	font-size: 4em
}

.uk-icon-5x {
	font-size: 5em
}

.uk-icon-fw {
	width: 1.28571em;
	text-align: center
}

.uk-icon-ul {
	padding-left: 0;
	margin-left: 2.14286em;
	list-style-type: none
}

.uk-icon-ul>li {
	position: relative
}

.uk-icon-li {
	position: absolute;
	left: -2.14286em;
	width: 2.14286em;
	top: .14286em;
	text-align: center
}

.uk-icon-li.uk-icon-lg {
	left: -1.85714em
}

.uk-icon-border {
	padding: .2em .25em .15em;
	border: solid .08em var(--faint);
	border-radius: .1em
}

.uk-icon-pull-left {
	float: left
}

.uk-icon-pull-right {
	float: right
}

.uk-icon.uk-icon-pull-left {
	margin-right: .3em
}

.uk-icon.uk-icon-pull-right {
	margin-left: .3em
}

.uk-icon.pull-left {
	margin-right: .3em
}

.uk-icon.pull-right {
	margin-left: .3em
}

.uk-icon-spin {
	-webkit-animation: fa-spin 2s infinite linear;
	animation: fa-spin 2s infinite linear
}

.uk-icon-pulse {
	-webkit-animation: fa-spin 1s infinite steps(8);
	animation: fa-spin 1s infinite steps(8)
}

.uk-icon-rotate-90 {
	-webkit-transform: rotate(90deg);
	-ms-transform: rotate(90deg);
	transform: rotate(90deg)
}

.uk-icon-rotate-180 {
	-webkit-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	transform: rotate(180deg)
}

.uk-icon-rotate-270 {
	-webkit-transform: rotate(270deg);
	-ms-transform: rotate(270deg);
	transform: rotate(270deg)
}

.uk-icon-flip-horizontal {
	-webkit-transform: scale(-1, 1);
	-ms-transform: scale(-1, 1);
	transform: scale(-1, 1)
}

.uk-icon-flip-vertical {
	-webkit-transform: scale(1, -1);
	-ms-transform: scale(1, -1);
	transform: scale(1, -1)
}

:root .uk-icon-flip-horizontal,
:root .uk-icon-flip-vertical,
:root .uk-icon-rotate-180,
:root .uk-icon-rotate-270,
:root .uk-icon-rotate-90 {
	filter: none
}

.uk-icon-stack {
	position: relative;
	display: inline-block;
	width: 2em;
	height: 2em;
	line-height: 2em;
	vertical-align: middle
}

.uk-icon-stack-1x,
.uk-icon-stack-2x {
	position: absolute;
	left: 0;
	width: 100%;
	text-align: center
}

.uk-icon-stack-1x {
	line-height: inherit
}

.uk-icon-stack-2x {
	font-size: 2em
}

.uk-icon-inverse {
