.uk-icon-small {
	font-size: 150%;
	vertical-align: -10%
}

.uk-icon-medium {
	font-size: 200%;
	vertical-align: -16%
}

.uk-icon-large {
	font-size: 250%;
	vertical-align: -22%
}

.uk-icon-justify {
	width: 1em;
	text-align: center
}

.uk-icon-spin {
	display: inline-block;
	-webkit-animation: uk-rotate 2s infinite linear;
	animation: uk-rotate 2s infinite linear
}

.uk-icon-hover {
	color: var(--medium-grey)
}

.uk-icon-hover:hover {
	color: var(--grey)
}

.uk-icon-button {
	box-sizing: border-box;
	display: inline-block;
	width: 35px;
	height: 35px;
	border-radius: 100%;
	background: var(--faint);
	line-height: 35px;
	color: var(--grey);
	font-size: 18px;
	text-align: center
}

.uk-icon-button:focus,
.uk-icon-button:hover {
	background-color: var(--faint);
	color: var(--grey);
	outline: 0
}

.uk-icon-button:active {
	background-color: var(--light-grey);
	color: var(--grey)
}
